(function ()
{
    'use strict';

    angular
        .module('app.core')
        .provider('msScrollConfig', msScrollConfigProvider)
        .directive('msScroll', msScrollDirective);

    /** @ngInject */
    function msScrollConfigProvider()
    {
        // Default configuration
        var defaultConfiguration = {
            wheelSpeed            : 1,
            wheelPropagation      : false,
            swipePropagation      : true,
            minScrollbarLength    : null,
            maxScrollbarLength    : null,
            useBothWheelAxes      : false,
            useKeyboard           : true,
            suppressScrollX       : false,
            suppressScrollY       : false,
            scrollXMarginOffset   : 0,
            scrollYMarginOffset   : 0,
            stopPropagationOnClick: true
        };

        // Methods
        this.config = config;

        //////////

        /**
         * Extend default configuration with the given one
         *
         * @param configuration
         */
        function config(configuration)
        {
            defaultConfiguration = angular.extend({}, defaultConfiguration, configuration);
        }

        /**
         * Service
         */
        this.$get = function ()
        {
            var service = {
                getConfig: getConfig
            };

            return service;

            //////////

            /**
             * Return the config
             */
            function getConfig()
            {
                return defaultConfiguration;
            }
        };
    }

    /** @ngInject */
    function msScrollDirective($timeout, msScrollConfig, msUtils, fuseConfig)
    {
        return {
            restrict: 'AE',
            compile : function (tElement)
            {
                // Do not replace scrollbars if
                // 'disableCustomScrollbars' config enabled
                if ( fuseConfig.getConfig('disableCustomScrollbars') )
                {
                    return;
                }

                // Do not replace scrollbars on mobile devices
                // if 'disableCustomScrollbarsOnMobile' config enabled
                if ( fuseConfig.getConfig('disableCustomScrollbarsOnMobile') && msUtils.isMobile() )
                {
                    return;
                }

                // Add class
                tElement.addClass('ms-scroll');

                return function postLink(scope, iElement, iAttrs)
                {
                    var options = {};

                    // If options supplied, evaluate the given
                    // value. This is because we don't want to
                    // have an isolated scope but still be able
                    // to use scope variables.
                    // We don't want an isolated scope because
                    // we should be able to use this everywhere
                    // especially with other directives
                    if ( iAttrs.msScroll )
                    {
                        options = scope.$eval(iAttrs.msScroll);
                    }

                    // Extend the given config with the ones from provider
                    options = angular.extend({}, msScrollConfig.getConfig(), options);

                    // Initialize the scrollbar
                    $timeout(function ()
                    {
                        PerfectScrollbar.initialize(iElement[0], options);
                    }, 0);

                    // Update the scrollbar on element mouseenter
                    iElement.on('mouseenter', updateScrollbar);

                    // Watch scrollHeight and update
                    // the scrollbar if it changes
                    scope.$watch(function ()
                    {
                        return iElement.prop('scrollHeight');
                    }, function (current, old)
                    {
                        if ( angular.isUndefined(current) || angular.equals(current, old) )
                        {
                            return;
                        }

                        updateScrollbar();
                    });

                    // Watch scrollWidth and update
                    // the scrollbar if it changes
                    scope.$watch(function ()
                    {
                        return iElement.prop('scrollWidth');
                    }, function (current, old)
                    {
                        if ( angular.isUndefined(current) || angular.equals(current, old) )
                        {
                            return;
                        }

                        updateScrollbar();
                    });

                    /**
                     * Update the scrollbar
                     */
                    function updateScrollbar()
                    {
                        PerfectScrollbar.update(iElement[0]);
                    }

                    // Cleanup on destroy
                    scope.$on('$destroy', function ()
                    {
                        iElement.off('mouseenter');
                        PerfectScrollbar.destroy(iElement[0]);
                    });
                };
            }
        };
    }
})();
(function ()
{
    'use strict';
	
   angular
        .module('app.core')
        .directive('msHoverIntent', directive);

    /** @ngInject */
    function directive($window)
    {
        return {
			 	restrict: 'A',
			    link: function(scope, element, attr) {
			    	element.hoverIntent({
			    	    over: showPopover,
			    	    out: hidePopover
			    	});
			    }
			  };
              
    }

    var showPopoverTimeout = undefined;
	var showPopover = function(event){
		window.clearTimeout(window.showPopoverTimeout);
		
		var targetEl=$(event.currentTarget);if(targetEl==undefined) return false;
		var targetElPosition=targetEl.offset();
		
		var top=targetElPosition.top;
		var left=targetElPosition.left;
		var height=targetEl.outerHeight();
		var width=targetEl.outerWidth();
		var padding=targetEl.parent().innerHeight()-height;
		
		var popupMainCss={
					top: top - padding,
					left: left - padding,
		};
		var popupInfoCss={
				top: 0,
				left: 0,
				width: width + 2 * padding,
				padding: padding
		};
		var modelInfoCss={
					marginTop: height + padding
		};
		var popupGridCss={
				top: 0,
				left: 'auto',
				right: 'auto',
				width:  popupInfoCss.width * 1.05,
				paddingTop: padding,
				paddingRight: 0,
				paddingBottom: 0,
				paddingLeft: 0,
				display: 'block'
		};
		var popupGridColumnCss={
				paddingRight: 0,
				paddingLeft: 0, 
				marginBottom: padding
		};
		
		var offsetParent=$('body');
		var offsetParentWidth=offsetParent.innerWidth();
		
		var shadowCss={};
		if(left < (offsetParent.offset().left+offsetParentWidth/2)){
			// position on right
				popupGridCss.left=popupInfoCss.width;
				popupGridColumnCss.paddingRight=padding;
				$('#model-image-popover').removeClass("model-on-right").addClass("model-on-left").css(shadowCss);
		}
		else{
			// position on left
				popupGridCss.left=-popupGridCss.width;
				popupGridColumnCss.paddingLeft=padding;
				$('#model-image-popover').removeClass("model-on-left").addClass("model-on-right").css(shadowCss);
				
		}
		
		
		// apply css
			$('#model-image-popover').css(popupMainCss);
			$('#model-image-popover-info').css(popupInfoCss);
			$('#model-image-popover-info #model-info').css(modelInfoCss);
			$('#model-image-popover-grid').css(popupGridCss);
			$('#model-image-popover-grid .grid-model-image-popover-column').css(popupGridColumnCss);
				
		
		// change lyer ordering
			targetEl.closest('.grid-masonry').find(".model").css({'z-index':0});
			targetEl.css({'z-index':29});
		
		window.showPopoverTimeout=window.setTimeout(function(){$('#model-image-popover').hide().fadeIn(100);}, 250);
	}
	
	var hidePopover = function(event){
		window.clearTimeout(window.showPopoverTimeout);
		$('#model-image-popover').fadeOut(100);
	}
})();
(function ()
{
    'use strict';

    angular
        .module('app.core')
        .directive('msCheck', function ($timeout, $parse) {
        return {
            require: 'ngModel',
            link: function ($scope, element, $attrs, ngModel) {
                return $timeout(function () {
                    var value;
                    value = $attrs['value'];

                    $scope.$watch($attrs['ngModel'], function (newValue) {
                        $(element).iCheck('update');
                    });

                    $scope.$watch($attrs['ngDisabled'], function (newValue) {
                        $(element).iCheck(newValue ? 'disable' : 'enable');
                        $(element).iCheck('update');
                    })

                    return $(element).iCheck({
                        checkboxClass: 'iradio_square-pink artist-check',
                        radioClass: 'iradio_flat-pink',
                        increaseArea: '20%'

                    }).on('ifChanged', function (event) {
                        if ($(element).attr('type') === 'checkbox' && $attrs['ngModel']) {
                            $scope.$apply(function () {
                                return ngModel.$setViewValue(event.target.checked);
                            })
                        }
                    }).on('ifClicked', function (event) {
                        if ($(element).attr('type') === 'radio' && $attrs['ngModel']) {
                            return $scope.$apply(function () {
                                //set up for radio buttons to be de-selectable
                                if (ngModel.$viewValue != value)
                                    return ngModel.$setViewValue(value);
                                else
                                    ngModel.$setViewValue(null);
                                ngModel.$render();
                                return
                            });
                        }
                    });
                });
            }
        };
});

})();
