(function ()
{
    'use strict';

    angular
        .module('app.wishlist')
        .controller('WishListController', controller);

   /** @ngInject */
    function controller($scope, $interval, $translate, store, $sce, msApi, $state, $stateParams, ngProgressLite, $timeout)
    {
       var self = this;
       this.$scope = $scope;
       this.$interval = $interval;
       this.$translate = $translate;
       this.artists = store.get('wishlist');
       this.store = store;
       this.msApi = msApi;
       this.$state = $state;
       this.$stateParams = $stateParams;
       this.$sce =  $sce;
       this.ngProgressLite = ngProgressLite;
       this.$timeout = $timeout;
       this.wishlist = {
           event_time : new Date()
       };
       this.resultMessage = '';
       this.imagePopover = {
           isActive  : false,
           requestId : null,
           images : [],
           artist : null
       }
       this.options = {
            format: "DD/MM/YYYY HH:mm:ss",
       }
       this.imagePopOverArray = [];
       this.msApi = msApi;
   
    }
    controller.prototype.destroyImagePopover = function(){
        var self = this;
        self.imagePopover.isActive = false;
    }

    controller.prototype.initImagePopover = function(event, artist){
        var self =  this;
        var requestId = artist.artist_id;
        if(requestId != self.imagePopover.requestId){
            self.imagePopover.images = self.imagesPopOverArray;
        }
        artist.infos = $.map(artist.artist_information, function(item, key){
            return {
                key : key,
                value : item
            }
        });
        self.imagePopover.requestId =requestId;
        self.imagePopover.isActive = true;
        self.imagePopover.artist  = artist;
        self.msApi.request('artists.files@query', {type : 'images_artist', 'artist_id' : artist.artist_id, page_size : 4}, function(resp){
             self.imagePopover.images = resp.items;
        }, function(err){

        });
	};
     controller.prototype.toogleWishList = function(event, artist){
        if(event){
            event.preventDefault();
            event.stopImmediatePropagation();
        }
        var self = this;
        var list = self.store.get('wishlist') == null ? [] :  self.store.get('wishlist');
        var indexData = -1;
        $.each(list, function(index ,item){
            if(artist.artist_id === item.artist_id){
                indexData = index;
                return false;
            }
        });
        if(indexData > -1){
            list.splice(indexData, 1);
            $('#model-image-popover').hide();
        }else{
            list.push(artist);
        }
        self.store.set('wishlist', list);
        return false;
		
	}

    controller.prototype.existed = function(artist){
        var self = this;
        var list = self.store.get('wishlist') == null ? [] :  self.store.get('wishlist') ;
        var indexData = -1;
        $.each(list, function(index ,item){
            if(artist.artist_id === item.artist_id){
                indexData = index;
                return false;
            }
        });
        return indexData > -1;
	}

    controller.prototype.sendWishList = function(){
        var self = this;
        if(self.$$submit) return;
        self.$$submit = true;
        var param = angular.copy(self.wishlist);
        var list  =  self.store.get('wishlist') == null ? [] :  self.store.get('wishlist') ;
        var map = $.map(list,function(item, index){
            return item.artist_id;
        });
        if(map.length <= 0) {
            alert('Xin hãy chọn nghệ sĩ');
            return false;
        }
        param['artists'] = map;
        var dateFormat =  moment(param.event_time, "DD/MM/YYYY HH:mm:ss").format("YYYY-MM-DDTHH:mm:ss") + 'Z';
        param['event_time'] = dateFormat;
        self.ngProgressLite.start();
        self.msApi.request('wishlist.event@insert', param, function(resp){
            self.store.remove('wishlist');
            self.resultMessage = 'CHÚC MỪNG QUÝ KHÁCH ĐÃ ĐĂNG KÝ THÀNH CÔNG. TIẾP TỤC KHÁM PHÁ THẾ GIỚI NGHỆ SĨ GOBY ART';
            self.$timeout(function(){
                 self.ngProgressLite.done();
                 self.$$submit = false;
            });
        }, function(error){
            alert('Có lỗi xảy ra vui lòng liên hệ quản trị viên');
            self.$timeout(function(){
                 self.ngProgressLite.done();
                 self.$$submit = false;
            });
        })
    }


})();

