(function ()
{
    'use strict';

    angular
        .module('fuse')
        .run(runBlock);

    /** @ngInject */
    function runBlock($window, $rootScope, $timeout, $state, store, $log, ngProgressLite, $translate)
    {
        if(window.site_option){
            var parsedBase64 = CryptoJS.enc.Base64.parse(window.site_option); //jshint ignore:line
            var data  = angular.fromJson(parsedBase64.toString(CryptoJS.enc.Utf8));
            var map = {};
            $.each(data, function(index, item){
                map[item.site_option_type + "_" + item.site_option_name] = item.site_option_value;
            });
            $rootScope.siteOption = map;
        }
        
        $rootScope.countWishList = function(){
            return store.get('wishlist') == null ? 0 : store.get('wishlist').length;
        }
        var md = new MobileDetect(window.navigator.userAgent);

        if(md.mobile() || md.tablet() || md.phone()){
            angular.element('html').addClass('is-touch')
        }else{
            angular.element('html').addClass('is-not-touch')
        }
        // Activate loading indicator
        var stateChangeStartEvent = $rootScope.$on('$stateChangeStart', function (event, toState, toParams)
        {
            $rootScope.loadingProgress = true;
            ngProgressLite.start();
            
        });

        // De-activate loading indicator
        var stateChangeSuccessEvent = $rootScope.$on('$stateChangeSuccess', function ()
        {
            $timeout(function ()
            {
                $("body").animate({scrollTop: 0}, "fast");
                ngProgressLite.done();
                $rootScope.loadingProgress = false;
            });
        });

        $rootScope.$on("event:error-server", function errorHandler($event, response, deferred) {

    

        });
        // Store state in the root scope for easy access
        $rootScope.state = $state;

        // Cleanup
        $rootScope.$on('$destroy', function ()
        {
            stateChangeStartEvent();
            stateChangeSuccessEvent();
        });
    }
})();
