$templateCache.put("app/main/about/contents/about.html","<div class=\"about-content parallax\"><div class=\"about-content-inner\"><div \nclass=\"content col_padding_bottom col_padding_top\"><div class=\"col col3_1 text_left\"><h2 \nclass=\"text-uppercase col_padding_top\">1. Giới Thiệu Chung</h2><hr class=\"white-bd white-fg\"><h3>\nGoby Art là trung tâm chuyên cung cấp nhân sự tổ chức sự kiện, với một cơ sở dữ liệu khổng lồ gồm các nhân sự chuyên nghiệp trong các lĩnh vực biểu diễn nghệ thuật: ca sỹ, nhóm nhảy, nhóm múa, MC, xiếc, ban nhạc, người mẫu ... với phong cách tự tin chuyên nghiệp sẽ làm sự kiện của bạn nổi bật. Chúng tôi tự tin là những chuyên gia trong lĩnh vực giải trí, bằng sự lắng nghe và thấu hiểu những băn khoăn của khách hàng, Goby Art sẽ tư vấn và đưa ra các giải pháp phù hợp với mức độ chuyên nghiệp của chương trình\n</h3></div><div class=\"col col3_2 text_left\"><h2 class=\"text-uppercase col_padding_top\">2. Dẫn đầu bởi sự khác biệt\n</h2><hr class=\"white-bd white-fg\"><h3>\nKhách hàng được “mục sở thị” nhân sự thông qua các mô tả chi tiết bằng hình ảnh và video clip tại website. Nhanh chóng tìm được nhân sự phù hợp với những yêu cầu của sự kiện như: chiều cao, cân nặng, số đo, ngoại ngữ... Kho dữ liệu nhân sự sự kiện lớn nhất Việt Nam, hỗ trợ tối đa việc tư vấn sự kiện, MIỄN PHÍ chụp hình sự kiện.\n</h3></div><div class=\"col col3_3 text_left\"><h2 class=\"text-uppercase col_padding_top\">3. Dẫn đầu bởi sự khác biệt\n</h2><hr class=\"white-bd white-fg\"><h3>\nChúng tôi là đối tác tin cậy của những Tập đoàn và công ty lớn: Tập đoàn Viễn thông Quân đội Viettel, Unilever Việt Nam, Tập đoàn EuroWindow ... Sự hài lòng và đánh giá cao của khách hàng là thước đo độ chuyên nghiệp của Goby Art. Cùng một mức chi phí, khách hàng sẽ tìm được nhiều lợi ích và giải pháp tối ưu nhất cho mình.\n</h3></div></div></div></div>");
